'use strict';

const li = document.querySelectorAll('li');

li.forEach(el => el.addEventListener('click',
  (curentEvent) => {
    if ([...curentEvent.target.children].length > 0) {
      const elStyle = el.children[0].style.display;
      const listShow = ((elStyle === 'block') || (elStyle === ''))
        ? 'none'
        : 'block';

      curentEvent.stopPropagation();
      curentEvent.currentTarget.children[0].style.display = listShow;
    }
  }
));
